header {
  background-color: $white;
  z-index: 1050;
  position: sticky;
  top: 0;
  box-shadow: 0rem -0.9rem 1rem black;

}

.fixedBar .header__logo {
  width: 15%;
  max-height: 60px;
  transition: all 200ms ease-in-out;
}

.header {
  display: flex;
  justify-content: space-between;

  &__nav {
    width: 100%;

    @include media-breakpoint-up(xl){
      width: 50%;
    }
  }

  &__logo {
    width: 25%;
    max-height: 110px;
    margin: 1rem 0 0 3rem;
    pointer-events: none;
    transition: all 200ms ease-in-out;

    @include media-breakpoint-down(lg){
      margin: 1rem 0 1rem 3rem;
    }

    img {
      width: 100%;
      height: auto;
    }
  }
}