.footer {
  position: relative;
  font-size: 1rem;
  margin-top: 2.5rem;
  padding: 2rem;
  overflow: hidden;
  background-color: $primary;

  ul {
    margin: 0;
    padding: 0;
  }

  li {

    list-style: none;
  }

  &__headline {
    font-size: $font-size-base;
  }

  &__background {
    --size: 20vh;
    border-radius: 50%;
    position: absolute;
    top: 0;
    overflow: hidden;

    --bigSize: calc(var(--size) * 1.25);
    background-color: $primary;

    height: var(--bigSize);
    width: var(--bigSize);
    right: calc(var(--bigSize) * -0.5);

    @include media-breakpoint-up(md){
      --size: 35vh;
    }

    @include media-breakpoint-up(lg){
      --size: 50vh;
    }

  }
}
