.main-header {

  margin-bottom: 2rem;
  overflow: hidden;

  &__wrapper {
    background-color: $secondary;
    box-shadow: 50vw 0 0 0 $secondary;
  }

  &__headline {
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    color: $white;
    margin-bottom: 2rem;
    padding: 1rem 0;
  }

}